import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
/**
 * Generated class for the HorariosPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-horarios',
  templateUrl: 'horarios.html',
})
export class HorariosPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController, public modalCtrl : ModalController) {
  }



  ionViewDidLoad() {

    let loading = this.loadingController.create({content : "cargando..."});
    loading.present();

    return this.apiProvider.getHorarioSemana()
    .then(data => {
       this.valoresDia = (Object.keys(data[0]));
       this.valoresClases = (Object.values(data[0]));

        this.valoresDia2 = (Object.keys(data[1]));
       this.valoresClases2 = (Object.values(data[1]));

       console.log(this.valoresClases);
    
    this.dataHorario = []; 
  //  console.log(data);
    loading.dismissAll();
    });

    }



 getHora(hora){

 let arr = hora.split(':');
 arr.pop();
 return arr.join(":");

  }


  goClase(clase){
  
   this.navCtrl.push('ClasePage', clase);
  //this.navCtrl.push('ClasePage');
  }

}



